.Menu {
  display: flex;
  justify-content: center;
  background-color: #282c34;
}

.Menu ul {
  list-style: none;
  margin: 0;
  width: 100%;
  text-align: center;
  padding: 0;
}

.Menu li {
  display: inline-block;
  margin: 24px;
}

.Menu a {
  text-decoration: none;
  color: white;
}

.Menu a:hover,
.Menu a:active,
.Menu a.active {
  color: grey;
  text-decoration: underline;
}
